@import url('https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Monomaniac+One&family=Montserrat:ital,wght@0,400;0,700;1,700&display=swap');

:root {
  --primary-color: #6C63FF;
  --white-color: #ffffff;
  --fonte-titulo: 'Montserrat', sans-serif;
  --fonte-texto: 'Open Sans', sans-serif;
  --font-texto2: 'Amatic SC', cursive;
  --appColor:  #3c3f45;
  
}

.App {
  background-image: url(data:image/png;base64,);
  mix-blend-mode: overlay;
  background-color: var(--appColor);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

::-webkit-scrollbar{
  display: none;
}

.main-container {
  width: 85vw;
  margin: auto;
}

h1 {
  font-family: var(--fonte-titulo);
  margin: 0;
}

p {
  font-family: var(--font-texto2);
  font-weight: bold;
  margin: 0;
}

.stroke-text {
  color: transparent;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-text-stroke-width: 4px;
  -webkit-text-stroke-color: var(--primary-color);
}

