.skills-container {
    display: flex;
    flex-direction: column;
}

.title-control {
    display: flex;
    align-items: center;
    justify-content: center;
}

.title-control>img {
    width: 2.2rem;
    height: 2.2rem;
    margin: 20px;
    padding: 10px;
    cursor: pointer;
    transition: transform 0.3s;
}

.title-control>img:hover {
    transform: scale(1.2);
}

.skills-title {
    text-align: center;
    font-size: 4rem;
    text-transform: uppercase;
    color: var(--white-color);
}

.skills-title>span {
    -webkit-text-stroke-width: 2px;
}

.skills-container>:nth-child(2) {
    text-align: center;
    font-size: 2rem;
    text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.616);
    letter-spacing: 2px;
    color: rgb(209, 209, 209);
    margin-top: 10px;
}

.hover-mouse-p {
    text-align: center;
    font-size: 1.5rem;
    letter-spacing: 2px;
    color: rgb(209, 209, 209);
    margin-top: -20px;
}

.skills {
    margin-top: 80px;
    display: flex;
    gap: 2rem;
    justify-content: center;
}

.skills>div {
    margin: 10px;
    padding: 3px;
    border-radius: 15px;
    background: linear-gradient(to bottom, var(--primary-color), transparent);
}

.skills>div>div{
    background-image: url(data:image/png;base64,);
    background-color: var(--appColor);
    padding: 40px;
    width: 600px;
    border-radius: 15px;
}

.skill-icon {
    width: 80px;
    height: 80px;
    padding: 30px;
    cursor: pointer;
    transition: transform 0.5s;
}

aside {
    width: 290px;
}

aside>:nth-child(1) {
    padding: 3px;
    border-radius: 15px;
    background: linear-gradient(to bottom, var(--primary-color), transparent);    
    margin: 10px;
}

aside>div>div {
    height: 355px;
    padding: 20px;
    border-radius: 15px;
    background-image: url(data:image/png;base64,);
    background-color: var(--appColor);
}

aside>div>div>h2 {
    margin: 0;
    margin-bottom: 20px;
    font-size: 1.9rem;
    color: var(--white-color);
    text-shadow: 2px 2px 1px rgba(0, 0, 0, 0.603);
}

aside>div>div>p {
    font-size: 1.3rem;
    color: var(--white-color);
    letter-spacing: 1px;
    font-family: var(--font-text);
}

aside>div>div>:nth-child(3) {
    color: var(--primary-color);
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.432);
}

.skill-icon:hover {
    transform: translateY(-10px) scale(1.1);
}

.section-select {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.section-select>div {
    display: flex;
    gap: 0.5rem;
    margin-top: -20px;
    margin-bottom: 20px;
}

.section-selected {
    width: 15px;
    height: 15px;
    background-color: white;
    border: 2px solid white;
    border-radius: 50%;
}

.section-no-selected{
    width: 15px;
    height: 15px;
    border: 2px solid white;
    border-radius: 50%;
}

@keyframes slide-right {
    from {
        transform: translateX(-200px);
    }
    to {
        transform: translateX(0);
    }
}

@media screen and (max-width: 768px) {
    .skills-container {
        width: 100vw;
    }

    .title-control>img {
        width: 1.5rem;
        height: 1.5rem;
        margin: 10px;
        padding: 5px;
    }

    .skills-title {
        font-size: 2.7rem;
    }

    .title-control {
        margin-bottom: 8px;
    }

    .section-select>p {
        font-size: 1.5rem;
    }

    .skills {
        flex-direction: column;
        align-items: center;
        margin-top: 40px;
    }

    .skills>div {
        margin: 20px;
        padding: 3px;
    }

    .skills>div>div {
        display: flex;
        width: fit-content;
        padding: 8px;
    }

    .skills>div>div>div {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
    }

    .hover-mouse-p {
        display: none;
    }

    .skill-icon {
        width: 60px;
        height: 60px;
        padding: 20px;
        cursor: pointer;
        transition: transform 0.5s;
    }

    aside {
        width: 90vw;
    }

    aside>div>div {
        height: 200px;
    }

    aside>div>div>p {
        font-size: 1.1rem;
        text-align: center;
    }
}